div.sideNav {
	position: fixed;
	top: 0;
	left: 0;
	width: 315px;
	height: 100%;
	background-color: #6cabdd;
	color: white;
}

@media screen and (max-width: 768px) {
	div.sideNav {
		width: 300px;
		transform: translateX(-300px);
	}
}

img.portrait {
	width: 220px;
	border-radius: 110px;
	margin-bottom: 20px;
	margin-top: 60px;
	box-shadow: 0 0 15px -5px rgba(0, 0, 0, 0.5);
}

h1.name {
	font-size: 40px;
	font-family: "Proza Libre", Arial, Helvetica, sans-serif;
	font-weight: 400;
}

ul.tabItems {
	margin-top: 50px;
	font-family: "Quicksand", Arial, Helvetica, sans-serif;
	font-size: 13px;
	text-transform: uppercase;
	letter-spacing: 1px;
	list-style: none;
	-webkit-padding-start: 0px;
	        padding-inline-start: 0px;
}

ul.tabItems li {
	margin: 20px 0px 20px 0px;
}

ul.tabItems li a {
	text-decoration: none;
	position: relative;
	color: white;
}

li a:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: #fff;
	visibility: hidden;
	transform: scaleX(0);
	transition: all 0.3s ease-in-out;
}

li a:hover:before {
	visibility: visible;
	transform: scaleX(1);
}

div.socialIcons {
	margin-top: 60px;
}

.socialIcon {
	color: #fff;
	width: 50px;
	height: auto;
	margin-left: 10px;
	margin-right: 10px;
}

div.headerContent {
	padding-top: 40px;
}

h1.header {
	font-size: 55px;
	font-family: "Proza Libre", Arial, Helvetica, sans-serif;
	font-weight: 400;
}

h2.header {
	font-size: 35px;
	font-family: "Proza Libre", Arial, Helvetica, sans-serif;
	font-weight: 400;
}

div.content {
	width: calc(100% - 315px);
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
}

@media screen and (max-width: 768px) {
	div.content {
		width: 100%;
	}
}

section.section {
	padding: 20px;
	margin-left: 10px;
	padding-top: 40px;
}

h2.subHeader {
	font-size: 30px;
	font-family: "Proza Libre", Arial, Helvetica, sans-serif;
	font-weight: 400;
	text-decoration: underline;
}

h3.descriptionHeader {
	font-size: 23px;
	font-family: "Proza Libre", Arial, Helvetica, sans-serif;
	font-weight: 400;
}

p.description {
	font-size: 20px;
	font-family: "Quicksand", Arial, Helvetica, sans-serif;
}

div.projectItem {
	display: flex;
	flex-direction: row;
	margin-left: 5px;
}

ul li {
	list-style: circle;
}

ul.projectsList {
	-webkit-padding-start: 20px;
	        padding-inline-start: 20px;
}

h1.projectHeader {
	font-size: 25px;
	font-family: "Quicksand", Arial, Helvetica, sans-serif;
	font-weight: 700;
}

div.optifyO {
	width: 70px;
	height: 70px;
	border-radius: 35px;
	background-color: #48c9b0;
}

p.optifyText {
	font-size: 45px;
	letter-spacing: 10px;
	color: #48c9b0;
	font-family: -apple-system, BlinkMacSystemFont, sans-serif;
	-webkit-margin-before: 0px;
	        margin-block-start: 0px;
	-webkit-margin-after: 0px;
	        margin-block-end: 0px;
	margin-left: 10px;
	margin-top: 15px;
}

p.description {
	font-size: 20px;
	font-family: "Quicksand", Arial, Helvetica, sans-serif;
}

div.contactInfo {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding-bottom: 25px;
}

.contactIcon {
	width: 25px;
	height: auto;
	margin-right: 20px;
}

p.contactText {
	font-size: 22px;
	font-family: "Quicksand", Arial, Helvetica, sans-serif;
	-webkit-margin-before: 0px;
	        margin-block-start: 0px;
	-webkit-margin-after: 0px;
	        margin-block-end: 0px;
}

a.link {
	text-decoration: none;
	position: relative;
	color: black;
}

a.link:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: black;
	visibility: hidden;
	transform: scaleX(0);
	transition: all 0.3s ease-in-out;
}

a.link:hover:before {
	visibility: visible;
	transform: scaleX(1);
}

